@import url(https://fonts.googleapis.com/css?family=Darker+Grotesque&display=swap);

/* Fonts */
/* Global */
body {
  margin: 0;
  padding-top: 76px;
  /* Actual height of nav is 63px */
  font-family: 'Darker Grotesque', sans-serif;
  font-size: 1.3em;
  overflow-x: hidden;
  background-color: white; }

h1, h2, h3, h4, h5 {
  text-transform: uppercase;
  color: #19396d;
  font-weight: 700; }

b {
  color: #ad8db2; }

ul li:before {
  font-family: 'FontAwesome';
  content: '\f067';
  margin: 0 5px 0 -15px;
  color: #f00;
  list-style-type: none; }

@media (min-width: 1600px) {
  .container {
    max-width: 1600px !important; } }

/* Common Elements */
.btn-primary {
  background-color: whitesmoke;
  border: 0;
  border-radius: 0;
  color: #19396d; }

.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open > .dropdown-toggle.btn-primary {
  color: white;
  background-color: #19396d;
  font-weight: bold;
  text-decoration: none !important; }

.no-underline:hover {
  text-decoration: none; }

a {
  color: #19396d; }

a:hover {
  color: #ad8db2; }

hr {
  color: whitesmoke; }

.container {
  max-width: 1600px; }

/* Navbar */
.navbar {
  background-color: white;
  -webkit-transition: background-color 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: background-color 0.3s ease-in-out; }
  .navbar .nav-link {
    text-transform: uppercase; }
  .navbar a {
    color: #19396d;
    font-weight: 700; }
  .navbar .navbar-brand {
    color: #19396d; }

.navbar-light .navbar-nav .nav-link {
  color: #19396d; }
  .navbar-light .navbar-nav .nav-link :hover {
    border-bottom: solid white 1px; }

.navbar-light .navbar-nav .active > .nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show > .nav-link {
  color: #19396d;
  border-bottom: solid #19396d 1px; }

.navbar-masthead {
  background-color: white;
  -webkit-transition: background-color 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: background-color 0.3s ease-in-out; }

/* Used for media atm */
/* Background media */
.media-container .media {
  top: 0;
  left: 0;
  z-index: -3;
  position: absolute;
  width: 100%;
  height: 100%; }

.media-container .media-overlay {
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-content: space-between;
  text-align: left;
  height: 70vh;
  padding: 2%;
  overflow-y: scroll;
  text-shadow: 2px 2px 2px #19396d;
  /**************
     TODO: Hide scroll bar on transactions page
    ***************/ }
  .media-container .media-overlay .overlay-wrapper {
    max-width: 1600px;
    align-self: center; }
  .media-container .media-overlay a {
    color: white; }
  .media-container .media-overlay h2 {
    font-weight: 300; }
  .media-container .media-overlay .header-wrapper {
    display: inline-block; }
    @media (max-width: 800px) {
      .media-container .media-overlay .header-wrapper h1 {
        font-size: 90%; } }
  @media (max-width: MEDIA_FONT_SIZE_BP) {
    .media-container .media-overlay .body-wrapper h2 {
      font-size: 100%; }
    .media-container .media-overlay .body-wrapper p {
      font-size: 60%; } }

.media-container h1, .media-container h2, .media-container p {
  color: white; }

.media-container hr {
  border: solid white 1px; }

/* Eye Catcher */
.eye-catcher-container {
  background-color: white;
  color: white;
  padding: 5%;
  font-size: 130%; }
  .eye-catcher-container h1, .eye-catcher-container h2, .eye-catcher-container h3, .eye-catcher-container p {
    color: #19396d; }

.split-images .split-image-column {
  color: white;
  min-height: 70vh;
  display: flex;
  justify-content: center;
  align-items: flex-start; }
  .split-images .split-image-column .split-image-content {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 5%; }
    .split-images .split-image-column .split-image-content p {
      font-size: 100%; }
  .split-images .split-image-column h4 {
    font-size: 250%;
    padding-top: 5%;
    color: white;
    text-transform: uppercase;
    font-weight: 700; }

/* presidio-aelf */
#presidio-aelf {
  text-align: left; }
  #presidio-aelf .card-title {
    text-transform: uppercase;
    font-size: 120%;
    color: #19396d;
    padding-bottom: 4%; }
  #presidio-aelf .combined-logos {
    max-width: 300px;
    float: right; }
  #presidio-aelf .card {
    margin-bottom: 5vh;
    border: 0;
    max-width: 600px;
    margin: 0 auto 5vh auto; }

/* Statistics */
#statistics {
  text-align: center;
  padding: 5% 0; }
  #statistics .counting-stat {
    font-size: 300%;
    color: #19396d;
    font-weight: 600; }

/* World Map */
#world-map {
  padding: 5% 0; }
  #world-map .map-wrapper {
    width: 900px; }
  #world-map .jvectormap-container {
    background-color: white !important; }

/* Mission Statement Words */
#mission-statement-words {
  background-image: url(/static/media/mission-statement.9a7efd17.jpg);
  background-position: center bottom;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 70vh;
  display: flex;
  align-content: center;
  align-items: center; }
  #mission-statement-words p, #mission-statement-words h2 {
    color: white; }
  #mission-statement-words p {
    font-size: 150%; }
  #mission-statement-words h2 {
    font-size: 250%; }

@media (max-width: 600px) {
  #mission-statement-words {
    background-position: center bottom; } }

/* Mission Statement */
#mission-statement {
  text-align: center;
  margin-bottom: 5%; }
  #mission-statement .icon {
    color: #6cb1d1; }
  #mission-statement .statement-text {
    font-size: 150%; }
  #mission-statement .emphasis {
    font-weight: bold; }
  #mission-statement .mechanism {
    text-transform: uppercase; }

/* Servies */
#services {
  color: white; }
  #services button {
    color: white; }
  #services .btn-primary:hover, #services .btn-primary:focus, #services .btn-primary:active, #services .btn-primary.active, #services .open > .dropdown-toggle.btn-primary {
    color: white;
    font-weight: bold;
    text-decoration: none !important; }
  #services h1, #services h2, #services h3, #services p {
    text-align: center;
    color: white; }
  #services .image-bg {
    background-position: top;
    background-attachment: fixed;
    background-repeat: no-repeat;
    padding-top: 2%; }
  #services .service {
    padding: 5% 0%; }
  @media (min-width: 1000px) {
    #services .service {
      padding: 5% 5%; } }

/* Our Team */
#our-team .thumbnail {
  padding-bottom: 10%;
  position: relative; }

#our-team img {
  border: solid 1px rgba(0, 0, 0, 0); }

#our-team img:hover {
  border: solid rgba(0, 0, 0, 0.7) 1px;
  box-shadow: 0px 10px 25px 10px rgba(0, 0, 0, 0.45);
  -webkit-transition: border 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: border 0.3s ease-in-out;
  -webkit-transition: box-shadow 0.3s ease-in-out;
  /* Safari prior 6.1 */
  transition: box-shadow 0.3s ease-in-out;
  cursor: pointer; }

#our-team .caption {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  bottom: 20%;
  left: 0;
  width: 100%;
  color: white;
  background-color: rgba(0, 0, 0, 0.2); }

#our-team .caption p {
  padding: 0;
  margin: 0; }

#our-team .caption .name {
  font-size: 110%; }

#our-team .caption .title {
  font-size: 80%; }

.team-bio-modal .modal-content {
  border-radius: 0; }

/* Portfolio slides */
.carousel-caption {
  background-color: rgba(0, 0, 0, 0.5); }
  .carousel-caption h3 {
    font-size: 2em;
    color: white; }
  .carousel-caption p {
    color: white; }

/* Recent Transactions */
#recent-transactions {
  padding: 5% 0; }
  #recent-transactions .card:hover {
    box-shadow: 0px 0px 5px 0px whitesmoke;
    -webkit-transition: border 0.25s ease-in-out;
    /* Safari prior 6.1 */
    transition: border 0.25s ease-in-out;
    -webkit-transition: box-shadow 0.25s ease-in-out;
    /* Safari prior 6.1 */
    transition: box-shadow 0.25s ease-in-out;
    cursor: pointer; }
  #recent-transactions .card-header {
    text-transform: uppercase;
    background-color: #19396d;
    color: white;
    font-weight: 500; }
  #recent-transactions .card-footer {
    background-color: white;
    color: #19396d; }
  #recent-transactions a:hover {
    text-decoration: none; }

/* Aircraft */
#engines .basic-content {
  background-color: whitesmoke;
  padding: 5% 0; }

/* Alternating column */
.even-row {
  background-color: whitesmoke; }

.odd-row {
  background-color: #19396d;
  color: white; }
  .odd-row h2 {
    color: white; }

.alternating-column-row {
  font-size: 120%; }
  .alternating-column-row .alternating-column-image-column {
    position: relative;
    padding: 0; }
  .alternating-column-row .alternating-column-content-column {
    display: flex;
    align-items: center;
    padding: 0% 3%; }
  .alternating-column-row image {
    padding: 0;
    margin: 0; }

/* Transactions */
/* Transactions */
#transactions {
  padding-bottom: 0; }
  #transactions #transaction-media-overlay {
    height: 100%; }
    #transactions #transaction-media-overlay h1 {
      font-size: 300%; }
    #transactions #transaction-media-overlay .transaction-row {
      margin: 5% 0; }
    #transactions #transaction-media-overlay .transaction-col {
      display: flex;
      align-items: center;
      align-content: flex-end;
      justify-content: center; }

/* Contact Us */
#contact-us {
  padding-bottom: 4%; }
  #contact-us .locations {
    line-height: 70%; }
  #contact-us #contact-us-content a {
    color: #19396d; }

/* Footer */
footer {
  background-color: #19396d;
  padding: 1% 0 0 0;
  color: white;
  text-align: left; }
  footer .column {
    padding: 1%; }
  footer .logo-col {
    text-align: center;
    padding: 2% 0; }
  footer .partner-image {
    max-height: 100px; }
  footer .contact-info {
    flex: 2 0 auto; }
  footer h3 {
    color: white; }
  footer hr {
    border-color: transparent; }
  footer a {
    color: white; }
  footer .contact-icon-wrapper .icon-margin {
    margin: 0 0 0 15px; }
  footer .social-icon-wrapper .icon-margin {
    margin: 0 15px 0 0; }

/* Copyright */
#copyright {
  padding: 0.5% 0;
  text-align: center; }

